.label {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
    color: #0066CC;
}

/*input[type="checkbox"] - no*/

.checkbox {
    appearance: none;
    /*Свойство appearance изменяет внешний вид элемента интерфейса, при сохранении его функции.
       Если задать значение none, то чекбокс пропадет, но по нему, тем не менее,
       можно щелкать и состояние чекбокса будет меняться*/
    width: 18px;
    height: 18px;
    margin-right: 8px;
    color: #0066CC;

    background-repeat: no-repeat;
    background-position: center center;

     background-size: 90% 90%;
    vertical-align: middle;
    border-radius: 3px;
    border: 2px solid #06c;
    cursor: pointer;
}

.checkbox:checked {
    appearance: none;

    background-image: url('checked.svg');
}

.checkbox:focus {
    border-color: #0059b3;
}

.spanClassName {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    line-height: 20px;
    color: #0066CC;
}
